import React from 'react';
import Map from './map';
import Geocode from "react-geocode";
const axios = require('axios');

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.changeAmount = this.changeAmount.bind(this);
    this.openCheckout = this.openCheckout.bind(this);
    this.state={
      isSubmit:false,
      isSetup:false,
      isPersonal:false,
      calcVal:{},
      toaddress:'',
      amount: 0
    }
  }

  changeAmount(e) {
    this.setState({amount: e.target.value})
  }

  openCheckout() {
    let options = {
      "key": "rzp_test_zfgh53To0jzwp9",
"amount": this.state.amount, // 2000 paise = INR 20, amount in paisa
"name": "Donkeycargo Pvt Ltd.,",
"description": "Online Shipping System",
"image": "./dist/img/DonkeyUser.png",
"handler": function (response){
  alert(response.razorpay_payment_id);
},
"prefill": {
  "name": "Karthic",
  "email": "karthi@gmail.com"
},
"notes": {
  "address": "Hello World"
},
"theme": {
  "color": "#F37254"
}
};

let rzp = new window.Razorpay(options);
rzp.open();
}

clickType = (type) =>{
  this.setState({ isSubmit:false });
  if(type==='submit') this.setState({ isSubmit:true });
  else
  {
    if(type==='setup')  this.setState({ isSetup:true });
    else if(type==='personal')  this.setState({ isPersonal:true });
  }
}
static defaultProps = {
  address:'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
  address1:'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
  fromlat:'9.925523',
  fromlng:'78.140323',
  tolat:'9.935103',
  tolng:'78.103698',
  isLocal:true
};
componentDidMount() 
{
  let fs='',ts='';
  if((this.props.address).includes('Tamil'))
    fs='tamil'
  else if((this.props.address).includes('kera'))
    fs='kera'

  if((this.props.address1).includes('Tamil'))
    ts='tamil'
  else if((this.props.address1).includes('kera'))
    ts='kera'

  console.log(fs+' - '+ts)
  axios.get(global.serverURL+"/api/courier/calc",{
    params: {
      "flat":this.props.fromlat,"flong":this.props.fromlng,
      "tlat":this.props.tolat,"tlong":this.props.tolng,
      "fromstate":fs,"tostate":ts,
      "couriertype":(this.props.isLocal)?'loc':'dom',"couriername":"",
      "DL1":"","DB1":"","DL2":"","DB2":"",
      "BL1":"","BB1":"","BH1":"","BL2":"","BB2":"","BH2":"",
      "couponcode":"","city":"chennai"
    }
  })
  .then((response) => {
    return response.data.data[0];
  })
  .then(data => {
    this.setState({ calcVal: data });
    Geocode.fromLatLng( data.clatitude , data.clongitude ).then(
      response => {
        const address = response.results[0].formatted_address;        
        this.setState({ toaddress:address,amount:(data.Total*100) });
      },
      error => {
        console.error(error);
      }
      );
  }).catch(error => {
    console.log(error);
  });


}


render()
{
  let res;
  let isCourier1=this.state.isCourier;
  let isParcel1=this.state.isParcel;
  const buttonstylefocus={
    textAlign: 'center',
    display: 'inline-block',
    padding: '13px 20px',
    borderRadius: '25px',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontWeight: '700',
    border: '1px solid #f4511e',
    color: '#fff',
    background: '#f4511e',
    boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)'
  };
  const buttonstyle={
    textAlign: 'center',
    display: 'inline-block',
    padding: '13px 20px',
    borderRadius: '10px',
    color: '#f4511e',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    border: '1px solid #f4511e',
    fontWeight: '700',
    background: 'transparent'
  };

  res = <div className="wrapper">
  <div className="">
  <div className="row">
  <div className="col-md-6 col-xs-12 text-center">

  <div className="card card-success card-outline">
  <div className="card-header">

  <h3 className="card-title text-center">
  <div className="row">
  <div className="col-3">
  <button className="btn1 btn-4 btn-4b" style={{float:'left',fontWeight:'500'}} onClick={()=>this.renderRedirect('back')}><i className="fa fa-arrow-left">&nbsp;Back</i></button>
  </div>
  <div className="col-6">
  <span className="textGradient"> Payment </span>
    <span className="textGradient"> Summary </span>
  </div>
  <div className="col-3">
  <button className="btn1 btn-4 btn-4a" style={{float:'right',fontWeight:'500'}} onClick={()=>this.renderRedirect('next')}><i className="fa fa-arrow-right">&nbsp;Next</i></button>
  </div>
  </div>
  </h3>
  </div>

  <div className="card-body" style={{padding: '0'}}>

  <div style={{ padding:'10px' }}>
  <label className="select2text">Delivery Location</label>
  <div className="input-group mb-3">
  <div className="input-group-prepend">

  <button type="button" className="btn btn-danger" style={{width: "70px"}}>To</button>
  </div>
  <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.state.toaddress }/>
  </div> 

  <div className="row">

  <div className="col-md-7 col-xs-7 mt-3" style={{fontSize:'25px'}}>
  <strong> Total Distance </strong>
  </div>
  <div className="col-md-5 col-xs-5 mt-3" style={{fontSize:'30px'}}>
  {this.state.calcVal.localdist} Kms
  </div>
  <div className="col-md-7 col-xs-7 mt-3" style={{fontSize:'25px'}}>
  <strong> Discount </strong>
  </div>
  <div className="col-md-5 col-xs-5 mt-3" style={{fontSize:'30px'}}>
  <i className="fa fa-inr fa-1x"></i><span>. {this.state.calcVal.disc}</span>
  </div>
  <div className="col-md-7 col-xs-7 mt-3" style={{fontSize:'25px'}}>
  <strong> Delivery Charges </strong> <i className="fa fa-info-circle" data-toggle="modal" data-target="#myModal"></i>
  </div>
  <div className="col-md-5 col-xs-5 mt-3" style={{fontSize:'30px'}}>
  <i className="fa fa-inr fa-1x"></i><span>. {this.state.calcVal.Total}</span>
  </div>

  </div>

  <div className="row mt-5">
  <div class="col-5 mt-2">
  <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link col-md-12 col-xs-12" onClick={()=>this.clickType("personal")}>Personal</a>
  </div>
  <div class="col-7 mt-2">
  <a href="#" style={this.state.isDomestic?buttonstylefocus:buttonstyle} className="orange-back-link col-md-12 col-xs-12" onClick={()=>this.clickType("setup")}>Setup Payment</a>
  </div>
  </div>

  <div className="row rowflexdisable mt-2" style={{paddingRight:'10px'}}>
  <button class="button col-12" onClick={this.openCheckout}><span>Continue</span></button>
  </div>

  </div>

  </div>

  </div>

  </div>

  <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh', width: '100%' }}>
  <img src="dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
  </div>

  </div>
  </div>

  <div id="myModal" className="modal fade" role="dialog">
  <div className="modal-dialog  modal-dialog-centered">

  <div className="modal-content">
  <div className="modal-header">
  <h4 className="modal-title">Payment Details</h4>
  <button type="button" className="close" data-dismiss="modal">&times;</button>
  </div>
  <div className="modal-body">
  <div className="row">
  <div class="col-6 mt-2">
  {this.state.calcVal.docHeader}    
  </div>
  <div class="col-6 mt-2">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.docamt}</span>
  </div>
  <div className="col-6 mt-2">
  {this.state.calcVal.boxHeader}
  </div>
  <div className="col-6 mt-2">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.boxamt}</span>
  </div>
  <div className="col-6 mt-2">
  Delivery Charges
  </div>
  <div className="col-6 mt-2">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.samt}</span>
  </div>
  <div className="col-6 offset-6">
  <hr />
  </div>
  <div className="col-6 mt-2">
  Gross
  </div>
  <div className="col-6 mt-2">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.gross}</span>
  </div>
  <div className="col-6 mt-2">
  Disc
  </div>
  <div className="col-6 mt-2">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.disc}</span>
  </div>
  <div className="col-6 offset-6">
  <hr />
  </div>
  <div className="col-6">
  Total
  </div>
  <div className="col-6">
  <i className="fa fa-inr"></i><span>. {this.state.calcVal.Total}</span>
  </div>
  <div className="col-6 offset-6">
  <hr />
  </div>
  </div>
  </div>
  <div className="modal-footer">
  <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
  </div>
  </div>

  </div>
  </div>

  </div>;

  if(isCourier1)
  {
    return <Map
    google={this.props.google}
    center={{lat: 9.909269, lng: 78.134126}}
    height='600px'
    zoom={15}
    isType='courier'
    mapHeader='Courier'
    />
  }
  else if(isParcel1)
  {
    return <Map
    google={this.props.google}
    center={{lat: 9.909269, lng: 78.134126}}
    height='600px'
    zoom={15}
    isType='parcel'
    mapHeader='Parcel'
    />
  }
  else
    return (res);
}
}

export default App1;
// background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
